@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900);
*{
    margin:0;
    padding: 0;
}
.hero{
    height:100vh;
    width:100%;
    background-image: url(/car-info/static/media/sky.350ae0c7.webp);
    background-size: cover;
    background-position:center;
    position: relative;
    overflow-x: hidden;
}
.highway{
    height: 200px;
    width:500%;
    display: block;
    background-image: url(/car-info/static/media/road.6efb0aea.webp);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-repeat: repeat-x;
    -webkit-animation: highway 5s linear infinite;
            animation: highway 5s linear infinite;
}
@-webkit-keyframes highway {

    100%{
        transform: translateX(-3400px) ;
    }

}
@keyframes highway {

    100%{
        transform: translateX(-3400px) ;
    }

}

.city{
    height: 250px;
    width: 500%;
    background-image: url(/car-info/static/media/city.c6733be7.png);
    position: absolute;
    bottom: 200px;
    left: 0;
    right: 0;
    display: block;
    z-index: 1;
    background-repeat: repeat-x;
    -webkit-animation: city 20s linear infinite;
            animation: city 20s linear infinite;
}
@-webkit-keyframes city {

    100%{
        transform: translateX(-1400px) ;
    }

}
@keyframes city {

    100%{
        transform: translateX(-1400px) ;
    }

}
.car{
    width: 400px;
    left:50%;
    bottom:100px;
    transform: translateX(-50%);
    position: absolute;
    z-index: 2;
}
.car img{
    width: 100%;
    -webkit-animation: car 1s linear infinite;
            animation: car 1s linear infinite;
}
@-webkit-keyframes car {

    100%{
        transform: translateY(-1px) ;
    }
    50%{
        transform: translateY(1px) ;
    }
    0%{
        transform: translateY(-1px) ;
    }

}
@keyframes car {

    100%{
        transform: translateY(-1px) ;
    }
    50%{
        transform: translateY(1px) ;
    }
    0%{
        transform: translateY(-1px) ;
    }

}

.wheel{
    left:50%;
    bottom: 178px;
    transform: translateX(-50%);
    position: absolute;
    z-index: 2;
}
.wheel img{
    width: 72px;
    height: 72px;
    -webkit-animation: wheel 1s linear infinite ;
            animation: wheel 1s linear infinite ;

}
.back-wheel{
    left:-165px;
    position: absolute;
}
.front-wheel{
    left:80px;
    position:absolute;
}
@media (max-width: 768px ) {
    .car {
        width: 300px
    }

    .wheel img{
        width:54px;
        height: 54px;
    }
    .back-wheel{
        left: -125px;
        top:16px
    }
    .front-wheel{
        left: 60px;
        top:16px
    }
    @-webkit-keyframes highway {

        100%{
            transform: translateX(-1400px) ;
        }

    }
    @keyframes highway {

        100%{
            transform: translateX(-1400px) ;
        }

    }

}
@-webkit-keyframes wheel {

    100%{
        transform: rotate(360deg);
    }

}
@keyframes wheel {

    100%{
        transform: rotate(360deg);
    }

}

.custom-model-main {
    text-align: center;
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; /* z-index: 1050; */
    -webkit-overflow-scrolling: touch;
    outline: 0;
    opacity: 0;
    transition: opacity 0.15s linear, z-index 0.15;
    z-index: -1;
    overflow-x: hidden;
    overflow-y: auto;
}

.model-open {
    z-index: 99999;
    opacity: 1;
    overflow: hidden;
}
.custom-model-inner {
    transform: translate(0, -25%);
    transition: transform 0.3s ease-out;
    display: inline-block;
    vertical-align: middle;
    width: 600px;
    margin: 30px auto;
    max-width: 97%;
}
.custom-model-wrap {
    display: block;
    width: 100%;
    position: relative;
    background-color: #fff;
    border: 1px solid #999;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
    background-clip: padding-box;
    outline: 0;
    text-align: left;
    padding: 20px;
    box-sizing: border-box;
    max-height: calc(100vh - 70px);
    overflow-y: auto;
}
.model-open .custom-model-inner {
    transform: translate(0, 0);
    position: relative;
    z-index: 999;
}
.model-open .bg-overlay {
    background: rgba(0, 0, 0, 0.6);
    z-index: 99;
}
.bg-overlay {
    background: rgba(0, 0, 0, 0);
    height: 100vh;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    transition: background 0.15s linear;
}
.close-btn {
    position: absolute;
    right: 0;
    top: -30px;
    cursor: pointer;
    z-index: 99;
    font-size: 30px;
    color: #fff;
}

@media screen and (min-width:800px){
    .custom-model-main:before {
        content: "";
        display: inline-block;
        height: auto;
        vertical-align: middle;
        margin-right: -0px;
        height: 100%;
    }
}
@media screen and (max-width:799px){
    .custom-model-inner{margin-top: 45px;}
}
.logo {
    position: fixed;
    top: 25px;
    left: 25px;
    display: block;
    z-index: 1000;
    transition: all 250ms linear;
}
.logo img {
    height: 50px;
    width: auto;
    display: block;
    transition: -webkit-filter 250ms 700ms linear;
    transition: filter 250ms 700ms linear;
    transition: filter 250ms 700ms linear, -webkit-filter 250ms 700ms linear;
}

:root {
    --rad: 0.7rem;
    --dur: 0.3s;
    --color-dark: #2f2f2f;
    --color-light: #fff;
    --color-brand: #57bd84;
    --font-fam: "Lato", sans-serif;
    --height: 5rem;
    --btn-width: 6rem;
    --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

html {
    box-sizing: border-box;
    height: 100%;
    font-size: 10px;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}

   form {
       position: relative;
       width: 70%;
       background: #57bd84;
       background: var(--color-brand);
       border-radius: 0.7rem;
       border-radius: var(--rad);
   }
form{
    position: absolute;
    top:20%;
    right: 50%;
    left:15%;
}
input,
button {
    height: 5rem;
    height: var(--height);
    font-family: "Lato", sans-serif;
    font-family: var(--font-fam);
    border: 0;
    color: #2f2f2f;
    color: var(--color-dark);
    font-size: 1.8rem;
}
input[type="search"] {
    outline: 0;
width: 100%;
    background: #fff;
    background: var(--color-light);
    padding: 0 1.6rem;
    border-radius: 0.7rem;
    border-radius: var(--rad);
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
transition: all 0.3s cubic-bezier(0, 0, 0.43, 1.49);
transition: all var(--dur) var(--bez);
    transition-property: width, border-radius;
    z-index: 1;
    position: relative;
}
button {
    display: none;
position: absolute;
    top: 0;
    right: 0;
    width: 6rem;
    width: var(--btn-width);
    font-weight: bold;
    background: #57bd84;
    background: var(--color-brand);
    border-radius: 0 0.7rem 0.7rem 0;
    border-radius: 0 var(--rad) var(--rad) 0;
}
input:not(:-ms-input-placeholder) {
    border-radius: 0.7rem 0 0 0.7rem;
    border-radius: var(--rad) 0 0 var(--rad);
    width: calc(100% - 6rem);
    width: calc(100% - var(--btn-width));
}
input:not(:placeholder-shown) {
    border-radius: 0.7rem 0 0 0.7rem;
    border-radius: var(--rad) 0 0 var(--rad);
    width: calc(100% - 6rem);
    width: calc(100% - var(--btn-width));
}
button {
    display: block;
}

label {
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden;
}



